import { Fragment } from 'react';
import { 
  Toolbar, 
  Box, 
  Divider 
} from "@mui/material";
import UndoRedoButtons from './toolbar/UndoRedoButtons';
import FontControls from './toolbar/FontControls';
import TextStyleButtons from './toolbar/TextStyleButtons';
import ColorButtons from './toolbar/ColorButtons';  // 新增
import AlignmentButtons from './toolbar/AlignmentButtons';
import ListButtons from './toolbar/ListButtons';
import MiscButtons from './toolbar/MiscButtons';

const EditorToolbar = ({ 
  editorState, 
  handlers, 
  fontHandlers, 
  imageHandlers,
  linkHandlers,
  embedHandlers,
  fileInputRef 
}) => {
  const buttonGroups = [
    {
      key: 'undo-redo',
      component: (
        <UndoRedoButtons 
          editorState={editorState}
          onUndo={handlers.handleUndo}
          onRedo={handlers.handleRedo}
        />
      )
    },
    {
      key: 'font-controls',
      component: (
        <FontControls 
          fontHandlers={fontHandlers}
        />
      )
    },
    {
      key: 'color-controls',
      component: (
        <ColorButtons 
          editorState={editorState}
          onInlineStyle={handlers.handleInlineStyle}
        />
      )
    },
    {
      key: 'text-style',
      component: (
        <TextStyleButtons 
          editorState={editorState}
          onInlineStyle={handlers.handleInlineStyle}
        />
      )
    },
    {
      key: 'alignment',
      component: (
        <AlignmentButtons 
          editorState={editorState}
          onAlignment={handlers.handleAlignment}
        />
      )
    },
    {
      key: 'list',
      component: (
        <ListButtons 
          editorState={editorState}
          onBlockType={handlers.handleBlockType}
        />
      )
    },
    {
      key: 'misc',
      component: (
        <MiscButtons 
          editorState={editorState}
          imageHandlers={imageHandlers}
          linkHandlers={linkHandlers}
          embedHandlers={embedHandlers} // 新增
          fileInputRef={fileInputRef}
        />
      )
    }
  ];

  return (
    <Toolbar 
      variant="dense" 
      sx={{ 
        minHeight: 48,
        borderBottom: '1px solid #e0e0e0',
        flexWrap: 'wrap',
        gap: 0,
        px: 1,
        py: 0.5,
        alignItems: 'flex-start',
      }}
    >
      {buttonGroups.map((group, index) => (
        <Fragment key={group.key}>
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              flexShrink: 0,
              minHeight: 40,
            }}
          >
            {group.component}
          </Box>
          
          {index < buttonGroups.length - 1 && (
            <Divider 
              orientation="vertical" 
              flexItem 
              sx={{ 
                mx: 1,
                alignSelf: 'stretch',
                minHeight: 30,
              }} 
            />
          )}
        </Fragment>
      ))}
    </Toolbar>
  );
};

export default EditorToolbar;