import { useRef } from 'react';
import ImageComponent from './components/ImageComponent';
import LinkComponent from './components/LinkComponent';
import EmbedComponent from './components/EmbedComponent'; // 新增
import { Box } from "@mui/material";
import { Editor } from "react-draft-wysiwyg";
import EditorToolbar from './components/EditorToolbar';
import { useEditorHandlers } from './hooks/useEditorHandlers';
import { useFontHandlers } from './hooks/useFontHandlers';
import { useImageHandlers } from './hooks/useImageHandlers';
import { useLinkHandlers } from './hooks/useLinkHandlers';
import { useEmbedHandlers } from './hooks/useEmbedHandlers'; // 新增
import { createStyleMap, getBlockStyle } from './utils/editorUtils';

const DraftJsWysiwyg = ({  
  editorState,  
  onEditorStateChange,  
  placeholder = "", 
  readOnly = false
}) => {
  const fileInputRef = useRef(null);
  
  const handlers = !readOnly ? useEditorHandlers(editorState, onEditorStateChange) : null;
  const fontHandlers = !readOnly ? useFontHandlers(editorState, onEditorStateChange) : null;
  const imageHandlers = !readOnly ? useImageHandlers(editorState, onEditorStateChange) : null;
  const linkHandlers = !readOnly ? useLinkHandlers(editorState, onEditorStateChange) : null;
  const embedHandlers = !readOnly ? useEmbedHandlers(editorState, onEditorStateChange) : null; // 新增

  const blockRendererFn = (block) => {
    if (block.getType() === 'atomic') {
      const entity = editorState.getCurrentContent().getEntity(block.getEntityAt(0));
      if (entity) {
        const entityType = entity.getType();
        
        if (entityType === 'IMAGE') {
          return {
            component: ImageComponent,
            editable: false,
          };
        }
        
        // 新增：处理嵌入网站
        if (entityType === 'EMBED') {
          return {
            component: EmbedComponent,
            editable: false,
            props: {
              onRemove: embedHandlers?.removeEmbed,
              readOnly: readOnly, // 传递 readOnly 属性
            },
          };
        }
      }
    }
    return null;
  };

  // 实体装饰器，用于渲染链接
  const decorators = [
    {
      strategy: (contentBlock, callback, contentState) => {
        contentBlock.findEntityRanges(
          (character) => {
            const entityKey = character.getEntity();
            return (
              entityKey !== null &&
              contentState.getEntity(entityKey).getType() === 'LINK'
            );
          },
          callback
        );
      },
      component: ({ children, entityKey, contentState }) => (
        <LinkComponent entityKey={entityKey} contentState={contentState}>
          {children}
        </LinkComponent>
      ),
    },
  ];

  // 修复：在 readonly 模式下也要创建完整的样式映射
  const FONT_SIZES = [10, 11, 12, 14, 16, 18, 20, 22, 24, 30, 36, 48, 60];
  const FONT_FAMILIES = ['Arial', 'Georgia', 'Impact', 'Tahoma', 'Times New Roman', 'Verdana'];
  
  const styleMap = createStyleMap(
    fontHandlers?.fontSizes || FONT_SIZES, 
    fontHandlers?.fontFamilies || FONT_FAMILIES
  );

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: !readOnly ? '100%' : 'auto', // 在非readonly模式下占据全部高度
        minHeight: 0, // 重要：允许flex子元素收缩
      }}
    >
      <style>{`
        .text-align-left { text-align: left !important; }
        .text-align-center { text-align: center !important; }
        .text-align-right { text-align: right !important; }
        .text-align-justify { text-align: justify !important; }
        .public-DraftEditor-content div[data-block="true"] {
          margin-bottom: 10px !important;
        }
      `}</style>

      {!readOnly && (
        <>
          <input
            type="file"
            ref={fileInputRef}
            style={{ display: 'none' }}
            accept="image/*"
            onChange={imageHandlers.handleFileUpload}
          />
          <Box sx={{ flexShrink: 0 }}> {/* 防止工具栏被压缩 */}
            <EditorToolbar
              editorState={editorState}
              handlers={handlers}
              fontHandlers={fontHandlers}
              imageHandlers={imageHandlers}
              linkHandlers={linkHandlers}
              embedHandlers={embedHandlers} // 新增
              fileInputRef={fileInputRef}
            />
          </Box>
        </>
      )}

      <Box
        sx={{
          width: !readOnly ? 870 : 'auto',
          maxWidth: '100%', // 防止溢出
          border: !readOnly ? 'none' : 'none',
          borderLeft: !readOnly ? '1px solid #e0e0e0' : 'none',
          borderRight: !readOnly ? '1px solid #e0e0e0' : 'none',
          borderBottom: !readOnly ? '1px solid #e0e0e0' : 'none',
          margin: !readOnly ? '0 auto' : 'none',
          padding: !readOnly ? '10px' : 'none',
          flex: !readOnly ? 1 : 'none', // 在非readonly模式下占据剩余空间
          display: 'flex',
          flexDirection: 'column',
          minHeight: 0, // 重要：允许flex子元素收缩
          overflow: !readOnly ? 'hidden' : 'visible', // 在非readonly模式下隐藏溢出
        }}
      >
        <Box
          sx={{
            flex: 1,
            overflow: !readOnly ? 'auto' : 'visible', // 在非readonly模式下允许滚动
            minHeight: !readOnly ? '400px' : 'auto', // 设置最小高度
            '& .rdw-editor-main': {
              height: !readOnly ? '100%' : 'auto',
              minHeight: !readOnly ? '100%' : 'auto',
            },
            '& .DraftEditor-root': {
              height: !readOnly ? '100%' : 'auto',
              minHeight: !readOnly ? '100%' : 'auto',
            },
            '& .DraftEditor-editorContainer': {
              height: !readOnly ? '100%' : 'auto',
              minHeight: !readOnly ? '100%' : 'auto',
            },
            '& .public-DraftEditor-content': {
              height: !readOnly ? '100%' : 'auto',
              minHeight: !readOnly ? '300px' : 'auto', // 给编辑器内容设置最小高度
            }
          }}
        >
          <Editor
            editorState={editorState}
            onEditorStateChange={onEditorStateChange}
            placeholder={placeholder}
            readOnly={readOnly}
            handleKeyCommand={handlers?.handleKeyCommand}
            customStyleMap={styleMap}
            blockStyleFn={getBlockStyle}
            blockRendererFn={blockRendererFn}
            decorators={decorators}
            toolbarHidden={true}
            mention={{
              separator: " ",
              trigger: "@",
            }}
            editorStyle={{
              height: !readOnly ? '100%' : 'auto',
              minHeight: !readOnly ? '100%' : 'auto',
            }}
          />
        </Box>
      </Box>
    </Box>
  );
};

export default DraftJsWysiwyg;