import { useState } from 'react';
import { 
  Box, 
  Paper, 
  Typography, 
  IconButton, 
  Tooltip,
  Link,
  Card,
  CardContent,
  CardActions,
  Button
} from '@mui/material';
import { 
  OpenInNew, 
  Delete, 
  Refresh,
  Language
} from '@mui/icons-material';

const EmbedComponent = ({ 
  block, 
  contentState, 
  blockProps 
}) => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  
  const entity = contentState.getEntity(block.getEntityAt(0));
  const { url, type = 'iframe', width = '100%', height = '400' } = entity.getData();
  
  // 获取 readonly 状态
  const isReadOnly = blockProps?.readOnly || false;

  const handleLoad = () => {
    setLoading(false);
    setError(false);
  };

  const handleError = () => {
    setLoading(false);
    setError(true);
  };

  const handleRefresh = () => {
    setLoading(true);
    setError(false);
    // Force iframe reload by changing src
    const iframe = document.querySelector(`iframe[data-embed-url="${url}"]`);
    if (iframe) {
      iframe.src = iframe.src;
    }
  };

  const handleDelete = () => {
    if (blockProps && blockProps.onRemove) {
      blockProps.onRemove(block.getKey());
    }
  };

  const getDomainFromUrl = (url) => {
    try {
      return new URL(url).hostname;
    } catch {
      return url;
    }
  };

  if (type === 'preview') {
    return (
      <Box sx={{ my: 2 }}>
        <Card variant="outlined">
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <Language sx={{ mr: 1, color: 'text.secondary' }} />
              <Typography variant="body2" color="text.secondary">
                {getDomainFromUrl(url)}
              </Typography>
            </Box>
            <Typography variant="h6" component="div" sx={{ mb: 1 }}>
              Embedded Website
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
              {url}
            </Typography>
          </CardContent>
          <CardActions>
            <Button 
              size="small" 
              startIcon={<OpenInNew />}
              onClick={() => window.open(url, '_blank')}
            >
              Open
            </Button>
            {/* 只在非 readonly 模式下显示 Remove 按钮 */}
            {!isReadOnly && (
              <Button 
                size="small" 
                startIcon={<Delete />}
                onClick={handleDelete}
                color="error"
              >
                Remove
              </Button>
            )}
          </CardActions>
        </Card>
      </Box>
    );
  }

  return (
    <Box sx={{ my: 2 }}>
      <Paper 
        variant="outlined" 
        sx={{ 
          overflow: 'hidden',
          position: 'relative'
        }}
      >
        {/* Header - 修复暗色模式兼容性 */}
        <Box sx={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'space-between',
          p: 1,
          bgcolor: (theme) => theme.palette.mode === 'dark' 
            ? 'grey.900' 
            : 'grey.50',
          borderBottom: '1px solid',
          borderColor: 'divider'
        }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Language sx={{ mr: 1, fontSize: 16, color: 'text.secondary' }} />
            <Typography variant="body2" color="text.secondary">
              {getDomainFromUrl(url)}
            </Typography>
          </Box>
          
          <Box>
            <Tooltip title="Refresh">
              <IconButton size="small" onClick={handleRefresh}>
                <Refresh fontSize="small" />
              </IconButton>
            </Tooltip>
            <Tooltip title="Open in new tab">
              <IconButton 
                size="small" 
                onClick={() => window.open(url, '_blank')}
              >
                <OpenInNew fontSize="small" />
              </IconButton>
            </Tooltip>
            {/* 只在非 readonly 模式下显示 Remove 按钮 */}
            {!isReadOnly && (
              <Tooltip title="Remove embed">
                <IconButton 
                  size="small" 
                  onClick={handleDelete}
                  color="error"
                >
                  <Delete fontSize="small" />
                </IconButton>
              </Tooltip>
            )}
          </Box>
        </Box>

        {/* Loading indicator - 优化暗色模式 */}
        {loading && (
          <Box sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'center',
            height: height + 'px',
            bgcolor: (theme) => theme.palette.mode === 'dark' 
              ? 'grey.800' 
              : 'grey.100'
          }}>
            <Typography variant="body2" color="text.secondary">
              Loading...
            </Typography>
          </Box>
        )}

        {/* Error state - 优化暗色模式 */}
        {error && (
          <Box sx={{ 
            display: 'flex', 
            flexDirection: 'column',
            alignItems: 'center', 
            justifyContent: 'center',
            height: height + 'px',
            bgcolor: (theme) => theme.palette.mode === 'dark' 
              ? 'grey.800' 
              : 'grey.100',
            textAlign: 'center',
            p: 2
          }}>
            <Typography variant="body2" color="error" gutterBottom>
              Failed to load website
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              This website may not allow embedding or may be temporarily unavailable.
            </Typography>
            <Box sx={{ mt: 2 }}>
              {/* 只在非 readonly 模式下显示 Try Again 按钮 */}
              {!isReadOnly && (
                <Button 
                  size="small" 
                  variant="outlined"
                  startIcon={<Refresh />}
                  onClick={handleRefresh}
                  sx={{ mr: 1 }}
                >
                  Try Again
                </Button>
              )}
              <Button 
                size="small" 
                variant="outlined"
                startIcon={<OpenInNew />}
                onClick={() => window.open(url, '_blank')}
              >
                Open in New Tab
              </Button>
            </Box>
          </Box>
        )}

        {/* IFrame */}
        <iframe
          data-embed-url={url}
          src={url}
          width={width}
          height={height + 'px'}
          style={{ 
            border: 'none',
            display: loading || error ? 'none' : 'block'
          }}
          onLoad={handleLoad}
          onError={handleError}
          title={`Embedded content from ${getDomainFromUrl(url)}`}
          sandbox="allow-scripts allow-same-origin allow-forms allow-popups allow-presentation"
        />
      </Paper>
    </Box>
  );
};

export default EmbedComponent;