import { Box, IconButton, Tooltip } from '@mui/material';
import { Undo, Redo } from '@mui/icons-material';

const UndoRedoButtons = ({ editorState, onUndo, onRedo }) => (
  <Box sx={{ display: 'flex', gap: 0 }}>
    <Tooltip title="Undo">
      <span>
      <IconButton 
        size="small" 
        onClick={onUndo}
        disabled={editorState.getUndoStack().size === 0}
      >
        <Undo fontSize="small" />
      </IconButton>
      </span>
    </Tooltip>
    <Tooltip title="Redo">
      <span>
      <IconButton 
        size="small" 
        onClick={onRedo}
        disabled={editorState.getRedoStack().size === 0}
      >
        <Redo fontSize="small" />
      </IconButton>
      </span>
    </Tooltip>
  </Box>
);

export default UndoRedoButtons;