import { useState } from 'react';
import { EditorState, AtomicBlockUtils } from 'draft-js';

export const useEmbedHandlers = (editorState, onEditorStateChange) => {
  const [embedDialogOpen, setEmbedDialogOpen] = useState(false);

  const insertEmbed = (embedData) => {
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'EMBED',
      'IMMUTABLE',
      embedData
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    const newEditorState = EditorState.set(editorState, {
      currentContent: contentStateWithEntity,
    });
    
    const finalEditorState = AtomicBlockUtils.insertAtomicBlock(
      newEditorState,
      entityKey,
      ' '
    );
    
    onEditorStateChange(finalEditorState);
  };

  const removeEmbed = (blockKey) => {
    const contentState = editorState.getCurrentContent();
    const blockMap = contentState.getBlockMap();
    const newBlockMap = blockMap.delete(blockKey);
    
    const newContentState = contentState.merge({
      blockMap: newBlockMap,
    });
    
    const newEditorState = EditorState.push(
      editorState,
      newContentState,
      'remove-range'
    );
    
    onEditorStateChange(newEditorState);
  };

  return {
    embedDialogOpen,
    setEmbedDialogOpen,
    insertEmbed,
    removeEmbed,
  };
};