import { Box, IconButton, Tooltip } from '@mui/material';
import {
  FormatListBulleted,
  FormatListNumbered
} from '@mui/icons-material';
import { getCurrentBlockData } from '../../utils/editorUtils';

const ListButtons = ({ editorState, onBlockType }) => {
  const { blockType } = getCurrentBlockData(editorState);

  const listButtons = [
    { type: 'unordered-list-item', icon: FormatListBulleted, title: 'Bullet List' },
    { type: 'ordered-list-item', icon: FormatListNumbered, title: 'Numbered List' }
  ];

  return (
    <Box sx={{ display: 'flex', gap: 0 }}>
      {listButtons.map(({ type, icon: Icon, title }) => (
        <Tooltip key={type} title={title}>
          <IconButton
            size="small"
            onClick={() => onBlockType(type)}
            color={blockType === type ? 'primary' : 'default'}
          >
            <Icon fontSize="small" />
          </IconButton>
        </Tooltip>
      ))}
    </Box>
  );
};

export default ListButtons;