import { Box, IconButton, Tooltip } from '@mui/material';
import {
  FormatAlignLeft,
  FormatAlignCenter,
  FormatAlignRight,
  FormatAlignJustify
} from '@mui/icons-material';
import { getCurrentBlockData } from '../../utils/editorUtils';

const AlignmentButtons = ({ editorState, onAlignment }) => {
  const { currentAlignment } = getCurrentBlockData(editorState);

  const alignmentButtons = [
    { alignment: 'left', icon: FormatAlignLeft, title: 'Align Left' },
    { alignment: 'center', icon: FormatAlignCenter, title: 'Align Center' },
    { alignment: 'right', icon: FormatAlignRight, title: 'Align Right' },
    { alignment: 'justify', icon: FormatAlignJustify, title: 'Justify' }
  ];

  return (
    <Box sx={{ display: 'flex', gap: 0 }}>
      {alignmentButtons.map(({ alignment, icon: Icon, title }) => (
        <Tooltip key={alignment} title={title}>
          <IconButton
            size="small"
            onClick={() => onAlignment(alignment)}
            color={currentAlignment === alignment ? 'primary' : 'default'}
          >
            <Icon fontSize="small" />
          </IconButton>
        </Tooltip>
      ))}
    </Box>
  );
};

export default AlignmentButtons;