
import { useState } from 'react';
import { EditorState, AtomicBlockUtils, Modifier } from 'draft-js';

export const useImageHandlers = (editorState, onEditorStateChange) => {
  const [imageDialogOpen, setImageDialogOpen] = useState(false);
  const [imageUrl, setImageUrl] = useState('');
  const [imageAlt, setImageAlt] = useState('');

  const insertImage = (src, alt = '', width = 300, height = 200, alignment = 'none') => {
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'IMAGE',
      'IMMUTABLE',
      { 
        src, 
        alt, 
        width: `${width}px`, // 保持与 ImageComponent 兼容的格式
        height: `${height}px` 
      }
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    
    // 创建新的编辑器状态
    let newEditorState = EditorState.set(editorState, {
      currentContent: contentStateWithEntity,
    });
    
    // 插入原子块
    newEditorState = AtomicBlockUtils.insertAtomicBlock(newEditorState, entityKey, ' ');
    
    // 如果有对齐设置，应用到新创建的块
    if (alignment && alignment !== 'none') {
      const currentContent = newEditorState.getCurrentContent();
      const selection = newEditorState.getSelection();
      const blockKey = selection.getStartKey();
      const block = currentContent.getBlockForKey(blockKey);
      
      // 设置块的对齐数据
      const newBlockData = block.getData().set('text-align', alignment);
      const newContentState = Modifier.setBlockData(
        currentContent,
        selection,
        newBlockData
      );
      
      newEditorState = EditorState.push(
        newEditorState,
        newContentState,
        'change-block-data'
      );
    }
    
    onEditorStateChange(newEditorState);
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const img = new Image();
        img.onload = () => {
          const maxWidth = 500;
          const maxHeight = 400;
          let { width, height } = img;
          
          if (width > maxWidth || height > maxHeight) {
            const ratio = Math.min(maxWidth / width, maxHeight / height);
            width *= ratio;
            height *= ratio;
          }
          
          insertImage(e.target.result, file.name, Math.round(width), Math.round(height));
        };
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    }
    event.target.value = '';
  };

  // 更新的 URL 插入处理函数
  const handleImageUrlInsert = (imageData) => {
    if (imageData && imageData.url) {
      insertImage(
        imageData.url,
        imageData.alt || '',
        imageData.width || 300,
        imageData.height || 200,
        imageData.alignment || 'none'
      );
    }
    
    // 清理状态
    setImageUrl('');
    setImageAlt('');
    setImageDialogOpen(false);
  };

  // 保持向后兼容的旧方法
  const handleImageUrlInsertLegacy = () => {
    if (imageUrl.trim()) {
      const img = new Image();
      img.onload = () => {
        const maxWidth = 500;
        const maxHeight = 400;
        let { width, height } = img;
        
        if (width > maxWidth || height > maxHeight) {
          const ratio = Math.min(maxWidth / width, maxHeight / height);
          width *= ratio;
          height *= ratio;
        }
        
        insertImage(imageUrl.trim(), imageAlt.trim(), Math.round(width), Math.round(height));
      };
      img.onerror = () => {
        insertImage(imageUrl.trim(), imageAlt.trim(), 300, 200);
      };
      img.src = imageUrl.trim();
      
      setImageUrl('');
      setImageAlt('');
      setImageDialogOpen(false);
    }
  };

  return {
    imageDialogOpen,
    setImageDialogOpen,
    imageUrl,
    setImageUrl,
    imageAlt,
    setImageAlt,
    handleFileUpload,
    handleImageUrlInsert, // 新的处理函数
    handleImageUrlInsertLegacy, // 向后兼容的处理函数
    insertImage // 导出基础插入函数供其他地方使用
  };
};