import { RichUtils } from 'draft-js';

const FONT_SIZES = [10, 11, 12, 14, 16, 18, 20, 22, 24, 30, 36, 48, 60];
const FONT_FAMILIES = ['Arial', 'Georgia', 'Times New Roman', "Impact", "Verdana"];

// 辅助函数：将字体名称转换为样式键
const getFontFamilyStyleKey = (fontFamily) => {
  return `FONTFAMILY_${fontFamily.replace(/\s+/g, '_').toUpperCase()}`;
};

export const useFontHandlers = (editorState, onEditorStateChange) => {
  const handleFontSize = (fontSize) => {
    const selection = editorState.getSelection();
    let newEditorState = editorState;
    
    // Remove existing font sizes
    FONT_SIZES.forEach(size => {
      if (newEditorState.getCurrentInlineStyle().has(`FONTSIZE_${size}`)) {
        newEditorState = RichUtils.toggleInlineStyle(newEditorState, `FONTSIZE_${size}`);
      }
    });
    
    onEditorStateChange(RichUtils.toggleInlineStyle(newEditorState, `FONTSIZE_${fontSize}`));
  };

  const handleFontFamily = (fontFamily) => {
    let newEditorState = editorState;
    
    // Remove existing font families - 使用正确的样式键格式
    FONT_FAMILIES.forEach(family => {
      const styleKey = getFontFamilyStyleKey(family);
      if (newEditorState.getCurrentInlineStyle().has(styleKey)) {
        newEditorState = RichUtils.toggleInlineStyle(newEditorState, styleKey);
      }
    });
    
    // 应用新的字体样式
    const newStyleKey = getFontFamilyStyleKey(fontFamily);
    onEditorStateChange(RichUtils.toggleInlineStyle(newEditorState, newStyleKey));
  };

  const getCurrentFontSize = () => {
    const currentStyle = editorState.getCurrentInlineStyle();
    return FONT_SIZES.find(size => currentStyle.has(`FONTSIZE_${size}`)) || '';
  };

  const getCurrentFontFamily = () => {
    const currentStyle = editorState.getCurrentInlineStyle();
    return FONT_FAMILIES.find(family => {
      const styleKey = getFontFamilyStyleKey(family);
      return currentStyle.has(styleKey);
    }) || '';
  };

  return {
    handleFontSize,
    handleFontFamily,
    getCurrentFontSize,
    getCurrentFontFamily,
    fontSizes: FONT_SIZES,
    fontFamilies: FONT_FAMILIES
  };
};