// ./services/BlogService.js
import api from './api';

const BlogService = {
  // 获取指定 ID 的博客
  getBlog: async (blogId, accessToken) => {
    try {
      const response = await api.get(`/blog/${blogId}/`, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
      return response.data;
    } catch (error) {
      console.error('Error fetching blog:', error);
      throw error;
    }
  },

  // 添加新博客
  addBlog: async (blogData, accessToken) => {
    try {
      // 1. 创建一个 FormData 对象
      const formData = new FormData();
  
      // 2. 将 blogData 对象中的每个字段添加到 formData
      for (const [key, value] of Object.entries(blogData)) {
        formData.append(key, value);
      }
  
      // 3. 发送 POST 请求，并设置请求头为 multipart/form-data
      const response = await api.post('/add_blog/', formData, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          'Content-Type': 'multipart/form-data'
        },
      });
  
      return response.data;
    } catch (error) {
      console.error('Error adding blog:', error);
      throw error;
    }
  },
  

  // 编辑博客（支持部分更新）
  editBlog: async (blogId, blogData, accessToken, isPartial = true) => {
      try {
          console.log(blogData);
          const method = isPartial ? 'patch' : 'put';

          // 创建 FormData 对象
          const formData = new FormData();
          for (const key in blogData) {
              if (blogData[key] !== undefined && blogData[key] !== null) {
                  formData.append(key, blogData[key]);
              }
          }

          const response = await api[method](`/edit_blog/${blogId}/`, formData, {
              headers: {
                  Authorization: `Bearer ${accessToken}`,
                  'Content-Type': 'multipart/form-data',
              },
          });

          return response.data;
      } catch (error) {
          console.error('Error editing blog:', error);
          throw error;
      }
  },

  toggleVisibility: async (blogId, accessToken) => {
    try {
      const response = await api.patch(`/edit_visibility/${blogId}/`, null, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
      return response.data;
    } catch (error) {
      console.error('Error toggling blog visibility:', error);
      throw error;
    }
  },

  getMeta: async (accessToken, page = null, pageSize = null) => {
    try {
      // 创建 FormData 对象
      const formData = new FormData();
      
      if (page !== null && pageSize !== null) {
        formData.append('page', page);
        formData.append('page_size', pageSize);
      }

      const response = await api.post('/get_blog_meta/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${accessToken}`,
        },
      });
      
      return response.data;
    } catch (error) {
      console.error('Error fetching blog metadata:', error);
      throw error;
    }
  },

  // 删除博客
  deleteBlog: async (blogId, accessToken) => {
    try {
      const response = await api.delete(`/delete_blog/${blogId}/`, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
      return response.status;
    } catch (error) {
      console.error('Error deleting blog:', error);
      throw error;
    }
  },

  // 获取草稿
  getDraft: async (username, accessToken) => {
    try {
      const response = await api.get(`/get_draft_blog/${username}/`, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
      return response.data;
    } catch (error) {
      console.error('Error fetching draft:', error);
      throw error;
    }
  },

  // 保存草稿
  saveDraft: async (draftData, accessToken) => {
    try {
      // 1. 创建一个 FormData 对象
      const formData = new FormData();
      // 2. 把 draftData 里的键值对添加到 formData
      for (const [key, value] of Object.entries(draftData)) {
        formData.append(key, value);
      }
  
      // 3. 发起 POST 请求，Content-Type 将会是 multipart/form-data
      const response = await api.post('/save_draft_blog/', formData, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          'Content-Type': 'multipart/form-data',
        },
      });
  
      return response.data;
    } catch (error) {
      console.error('Error saving draft:', error);
      throw error;
    }
  },
  
};

export default BlogService;
