import { useState } from 'react';
import { 
  Box, 
  IconButton, 
  Tooltip, 
  Popover, 
  Grid,
  Typography
} from '@mui/material';
import {
  FormatColorText,
  FormatColorFill
} from '@mui/icons-material';

const ColorButtons = ({ editorState, onInlineStyle }) => {
  const [textColorAnchor, setTextColorAnchor] = useState(null);
  const [highlightAnchor, setHighlightAnchor] = useState(null);
  
  const currentInlineStyle = editorState.getCurrentInlineStyle();

  // 预定义颜色选项
  const colors = [
    { name: 'Black', value: '#000000' },
    { name: 'Red', value: '#FF0000' },
    { name: 'Green', value: '#00FF00' },
    { name: 'Blue', value: '#0000FF' },
    { name: 'Orange', value: '#FFA500' },
    { name: 'Purple', value: '#800080' },
    { name: 'Pink', value: '#FFC0CB' },
    { name: 'Brown', value: '#A52A2A' },
    { name: 'Gray', value: '#808080' },
    { name: 'Navy', value: '#000080' },
    { name: 'Teal', value: '#008080' },
    { name: 'Maroon', value: '#800000' }
  ];

  const highlightColors = [
    { name: 'None', value: 'transparent' },
    { name: 'Yellow', value: '#FFFF00' },
    { name: 'Green', value: '#90EE90' },
    { name: 'Pink', value: '#FFB6C1' },
    { name: 'Blue', value: '#ADD8E6' },
    { name: 'Orange', value: '#FFE4B5' },
    { name: 'Purple', value: '#DDA0DD' },
    { name: 'Red', value: '#FFA07A' },
    { name: 'Gray', value: '#D3D3D3' }
  ];

  const handleTextColorClick = (event) => {
    setTextColorAnchor(event.currentTarget);
  };

  const handleHighlightClick = (event) => {
    setHighlightAnchor(event.currentTarget);
  };

  const handleTextColorClose = () => {
    setTextColorAnchor(null);
  };

  const handleHighlightClose = () => {
    setHighlightAnchor(null);
  };

  const applyTextColor = (color) => {
    // 移除所有现有的文字颜色样式
    let newStyle = currentInlineStyle;
    colors.forEach(c => {
      const styleKey = `COLOR_${c.value.replace('#', '')}`;
      if (newStyle.has(styleKey)) {
        newStyle = newStyle.remove(styleKey);
      }
    });

    // 应用新的文字颜色
    const colorStyle = `COLOR_${color.replace('#', '')}`;
    onInlineStyle(colorStyle);
    handleTextColorClose();
  };

  const applyHighlightColor = (color) => {
    // 移除所有现有的高亮颜色样式
    let newStyle = currentInlineStyle;
    highlightColors.forEach(c => {
      const styleKey = `HIGHLIGHT_${c.value.replace('#', '').replace('transparent', 'NONE')}`;
      if (newStyle.has(styleKey)) {
        newStyle = newStyle.remove(styleKey);
      }
    });

    // 应用新的高亮颜色
    const highlightStyle = `HIGHLIGHT_${color.replace('#', '').replace('transparent', 'NONE')}`;
    onInlineStyle(highlightStyle);
    handleHighlightClose();
  };

  // 检查当前是否有文字颜色样式
  const hasTextColor = colors.some(color => 
    currentInlineStyle.has(`COLOR_${color.value.replace('#', '')}`)
  );

  // 检查当前是否有高亮样式
  const hasHighlight = highlightColors.some(color => 
    currentInlineStyle.has(`HIGHLIGHT_${color.value.replace('#', '').replace('transparent', 'NONE')}`)
  );

  const ColorPalette = ({ colors, onColorSelect, title }) => (
    <Box sx={{ p: 2, width: 240 }}>
      <Typography variant="subtitle2" sx={{ mb: 1 }}>
        {title}
      </Typography>
      <Grid container spacing={1}>
        {colors.map((color) => (
          <Grid item key={color.name}>
            <Tooltip title={color.name}>
              <Box
                onClick={() => onColorSelect(color.value)}
                sx={{
                  width: 24,
                  height: 24,
                  backgroundColor: color.value === 'transparent' ? '#fff' : color.value,
                  border: color.value === 'transparent' ? '2px solid #ddd' : '1px solid #ddd',
                  borderRadius: 1,
                  cursor: 'pointer',
                  position: 'relative',
                  '&:hover': {
                    transform: 'scale(1.1)',
                    boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
                  },
                  ...(color.value === 'transparent' && {
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      top: '50%',
                      left: '10%',
                      right: '10%',
                      height: '2px',
                      backgroundColor: '#ff0000',
                      transform: 'translateY(-50%) rotate(45deg)',
                    }
                  })
                }}
              />
            </Tooltip>
          </Grid>
        ))}
      </Grid>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex', gap: 0.2}}>
      {/* 文字颜色按钮 */}
      <Tooltip title="Text Color">
        <IconButton
          size="small"
          onClick={handleTextColorClick}
          color={hasTextColor ? 'primary' : 'default'}
        >
          <FormatColorText fontSize="small" />
        </IconButton>
      </Tooltip>

      {/* 高亮颜色按钮 */}
      <Tooltip title="Highlight Color">
        <IconButton
          size="small"
          onClick={handleHighlightClick}
          color={hasHighlight ? 'primary' : 'default'}
        >
          <FormatColorFill fontSize="small" />
        </IconButton>
      </Tooltip>

      {/* 文字颜色选择器弹窗 */}
      <Popover
        open={Boolean(textColorAnchor)}
        anchorEl={textColorAnchor}
        onClose={handleTextColorClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <ColorPalette 
          colors={colors} 
          onColorSelect={applyTextColor}
          title="Text Color"
        />
      </Popover>

      {/* 高亮颜色选择器弹窗 */}
      <Popover
        open={Boolean(highlightAnchor)}
        anchorEl={highlightAnchor}
        onClose={handleHighlightClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <ColorPalette 
          colors={highlightColors} 
          onColorSelect={applyHighlightColor}
          title="Highlight Color"
        />
      </Popover>
    </Box>
  );
};

export default ColorButtons;