import { Box, IconButton, Tooltip } from '@mui/material';
import {
  FormatBold,
  FormatItalic,
  FormatUnderlined,
  FormatStrikethrough,
  Superscript,
  Subscript,
  Code
} from '@mui/icons-material';

const TextStyleButtons = ({ editorState, onInlineStyle }) => {
  const currentInlineStyle = editorState.getCurrentInlineStyle();

  const styleButtons = [
    { style: 'BOLD', icon: FormatBold, title: 'Bold' },
    { style: 'ITALIC', icon: FormatItalic, title: 'Italic' },
    { style: 'UNDERLINE', icon: FormatUnderlined, title: 'Underline' },
    { style: 'STRIKETHROUGH', icon: FormatStrikethrough, title: 'Strikethrough' },
    { style: 'SUPERSCRIPT', icon: Superscript, title: 'Superscript' },
    { style: 'SUBSCRIPT', icon: Subscript, title: 'Subscript' },
    { style: 'CODE', icon: Code, title: 'Code' }
  ];

  return (
    <Box sx={{ display: 'flex', gap: 0 }}>
      {styleButtons.map(({ style, icon: Icon, title }) => (
        <Tooltip key={style} title={title}>
          <IconButton
            size="small"
            onClick={() => onInlineStyle(style)}
            color={currentInlineStyle.has(style) ? 'primary' : 'default'}
          >
            <Icon fontSize="small" />
          </IconButton>
        </Tooltip>
      ))}
    </Box>
  );
};

export default TextStyleButtons;