import { useState, useRef, useEffect } from 'react';
import { EditorState } from 'draft-js';

// Image component for rendering in editor - compatible with draft-js-wysiwyg original format
const ImageComponent = ({ block, contentState, editorState, onEditorStateChange, readOnly }) => {
  const entity = contentState.getEntity(block.getEntityAt(0));
  const entityData = entity.getData();
  
  // Compatible with original draft-js-wysiwyg format
  const src = entityData.src;
  const alt = entityData.alt;
  // Original format uses 'height' and 'width' as strings with units or just numbers
  const originalWidth = entityData.width;
  const originalHeight = entityData.height;
  
  // Parse width and height, handling both string and number formats
  const parseSize = (size, defaultValue) => {
    if (!size) return defaultValue;
    if (typeof size === 'number') return size;
    if (typeof size === 'string') {
      // Remove 'px', '%', or other units and convert to number
      const numValue = parseInt(size.replace(/[^\d]/g, ''));
      return isNaN(numValue) ? defaultValue : numValue;
    }
    return defaultValue;
  };
  
  const [isSelected, setIsSelected] = useState(false);
  const [isResizing, setIsResizing] = useState(false);
  const [currentWidth, setCurrentWidth] = useState(() => parseSize(originalWidth, 300));
  const [currentHeight, setCurrentHeight] = useState(() => parseSize(originalHeight, 200));
  const imageRef = useRef(null);
  const containerRef = useRef(null);
  const alignment = block.getData().get('text-align') || 'none';
  
  // Handle image selection
  const handleImageClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsSelected(true);
  };

  // Handle click outside to deselect
  useEffect(() => {
    const handleClickOutside = (e) => {
      if (containerRef.current && !containerRef.current.contains(e.target)) {
        setIsSelected(false);
      }
    };

    if (isSelected) {
      document.addEventListener('mousedown', handleClickOutside);
      return () => document.removeEventListener('mousedown', handleClickOutside);
    }
  }, [isSelected]);

  // Update image size in entity - maintain original format
  const updateImageSize = (newWidth, newHeight) => {
    if (!editorState || !onEditorStateChange) {
      console.warn('EditorState or onEditorStateChange is not available');
      return;
    }

    try {
      const contentState = editorState.getCurrentContent();
      const entityKey = block.getEntityAt(0);
      const entity = contentState.getEntity(entityKey);
      
      // Keep original format - store as string with 'px' unit like original
      const newEntityData = { 
        ...entity.getData(), 
        width: `${newWidth}px`, 
        height: `${newHeight}px` 
      };
      
      const newContentState = contentState.replaceEntityData(entityKey, newEntityData);
      const newEditorState = EditorState.push(
        editorState,
        newContentState,
        'apply-entity'
      );
      onEditorStateChange(newEditorState);
    } catch (error) {
      console.error('Error updating image size:', error);
    }
  };

  // Handle resize drag
  const handleResizeStart = (e, direction) => {
    e.preventDefault();
    e.stopPropagation();
    setIsResizing(true);
    
    const startX = e.clientX;
    const startY = e.clientY;
    const startWidth = currentWidth;
    const startHeight = currentHeight;
    const aspectRatio = startWidth / startHeight;

    const handleMouseMove = (e) => {
      const deltaX = e.clientX - startX;
      const deltaY = e.clientY - startY;
      
      let newWidth, newHeight;
      
      if (direction === 'se') {
        // Bottom-right corner - maintain aspect ratio
        newWidth = Math.max(50, startWidth + deltaX);
        newHeight = newWidth / aspectRatio;
      } else if (direction === 'e') {
        // Right edge
        newWidth = Math.max(50, startWidth + deltaX);
        newHeight = newWidth / aspectRatio;
      } else if (direction === 's') {
        // Bottom edge
        newHeight = Math.max(50, startHeight + deltaY);
        newWidth = newHeight * aspectRatio;
      }
      
      setCurrentWidth(Math.min(newWidth, 800)); // Max width limit
      setCurrentHeight(Math.min(newHeight, 600)); // Max height limit
    };

    const handleMouseUp = () => {
      setIsResizing(false);
      
      // Update entity data with safety check
      if (editorState && onEditorStateChange) {
        updateImageSize(currentWidth, currentHeight);
      }
      
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  // Get container style based on alignment (compatible with original)
  const getContainerStyle = () => {
    let textAlign = 'none';
    if (alignment === 'left') textAlign = 'left';
    else if (alignment === 'right') textAlign = 'right';
    else if (alignment === 'center') textAlign = 'center';
    
    return {
      textAlign: textAlign !== 'none' ? textAlign : 'left',
      margin: '10px 0',
      position: 'relative',
      display: 'inline-block',
      cursor: isSelected ? 'move' : 'pointer'
    };
  };

  return (
    <div 
      ref={containerRef}
      style={getContainerStyle()}
      onClick={handleImageClick}
    >
      <img
        ref={imageRef}
        src={src}
        alt={alt || ''}
        style={{
          width: `${currentWidth}px`,
          height: `${currentHeight}px`,
          border: isSelected ? '2px solid #1976d2' : 'none',
          borderRadius: isSelected ? '4px' : '0',
          display: 'block',
          margin: alignment === 'center' ? '0 auto' : '0'
        }}
        draggable={false}
      />
      
      {/* Resize handles */}
      {isSelected && !readOnly && (
        <>
          {/* Corner handle (bottom-right) */}
          <div
            style={{
              position: 'absolute',
              bottom: -4,
              right: -4,
              width: 8,
              height: 8,
              backgroundColor: '#1976d2',
              cursor: 'se-resize',
              borderRadius: '50%'
            }}
            onMouseDown={(e) => handleResizeStart(e, 'se')}
          />
          
          {/* Right edge handle */}
          <div
            style={{
              position: 'absolute',
              right: -4,
              top: '50%',
              transform: 'translateY(-50%)',
              width: 8,
              height: 20,
              backgroundColor: '#1976d2',
              cursor: 'e-resize',
              borderRadius: '4px'
            }}
            onMouseDown={(e) => handleResizeStart(e, 'e')}
          />
          
          {/* Bottom edge handle */}
          <div
            style={{
              position: 'absolute',
              bottom: -4,
              left: '50%',
              transform: 'translateX(-50%)',
              width: 20,
              height: 8,
              backgroundColor: '#1976d2',
              cursor: 's-resize',
              borderRadius: '4px'
            }}
            onMouseDown={(e) => handleResizeStart(e, 's')}
          />

          {/* Size info */}
          <div style={{
            position: 'absolute',
            top: -30,
            left: '50%',
            transform: 'translateX(-50%)',
            backgroundColor: 'rgba(0,0,0,0.8)',
            color: 'white',
            padding: '4px 8px',
            borderRadius: '4px',
            fontSize: '12px',
            whiteSpace: 'nowrap'
          }}>
            <span>{Math.round(currentWidth)} × {Math.round(currentHeight)}</span>
          </div>
        </>
      )}
    </div>
  );
};

export default ImageComponent;