import { useState } from 'react';
import { 
  Box, 
  IconButton, 
  Tooltip, 
  Button, 
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@mui/material';
import { createRoot } from 'react-dom/client';
import {
  Link,
  LinkOff,
  Image as ImageIcon
} from '@mui/icons-material';
import DatasetLinkedIcon from '@mui/icons-material/DatasetLinked';
import ImageUrlDialog from './ImageUrlDialog'; // 引入我们刚创建的组件

const MiscButtons = ({ editorState, imageHandlers, fileInputRef, linkHandlers, embedHandlers }) => {
  const [linkDialogOpen, setLinkDialogOpen] = useState(false);
  const [linkUrl, setLinkUrl] = useState('');
  const [linkText, setLinkText] = useState('');
  const [isEditingLink, setIsEditingLink] = useState(false);
  
  // Embedded website states
  const [embedDialogOpen, setEmbedDialogOpen] = useState(false);
  const [embedUrl, setEmbedUrl] = useState('');
  const [embedType, setEmbedType] = useState('iframe');
  const [embedWidth, setEmbedWidth] = useState('100%');
  const [embedHeight, setEmbedHeight] = useState('400');

  const openFilePicker = () => {
    fileInputRef.current?.click();
  };

  const handleImageMenuClick = (event) => {
    const rect = event.currentTarget.getBoundingClientRect();
    const menu = (
      <Box sx={{
        position: 'fixed',
        top: rect.bottom + 5,
        left: rect.left,
        bgcolor: 'background.paper',
        boxShadow: 3,
        borderRadius: 1,
        p: 1,
        zIndex: 1000,
        minWidth: 150
      }}>
        <Button
          size="small"
          fullWidth
          onClick={() => {
            openFilePicker();
            document.getElementById('image-menu')?.remove();
          }}
          sx={{ mb: 1, justifyContent: 'flex-start', textTransform: 'none' }}
        >
          From Local
        </Button>
        <Button
          size="small"
          fullWidth
          onClick={() => {
            imageHandlers.setImageDialogOpen(true);
            document.getElementById('image-menu')?.remove();
          }}
          sx={{ justifyContent: 'flex-start', textTransform: 'none' }}
        >
          From URL
        </Button>
      </Box>
    );
    
    // Remove existing menu
    document.getElementById('image-menu')?.remove();
    
    // Create new menu
    const menuElement = document.createElement('div');
    menuElement.id = 'image-menu';
    document.body.appendChild(menuElement);
    
    const root = createRoot(menuElement);
    root.render(menu);
    
    // Click outside to close
    const handleClickOutside = () => {
      document.getElementById('image-menu')?.remove();
      document.removeEventListener('click', handleClickOutside);
    };
    setTimeout(() => {
      document.addEventListener('click', handleClickOutside);
    }, 100);
  };

  const handleLinkClick = () => {
    const selection = editorState.getSelection();
    const selectedText = editorState.getCurrentContent()
      .getBlockForKey(selection.getStartKey())
      .getText()
      .slice(selection.getStartOffset(), selection.getEndOffset());
    
    // Check if current selection is already a link
    const currentEntity = linkHandlers.getCurrentLinkEntity(editorState);
    
    if (currentEntity) {
      // Editing existing link
      const entityData = editorState.getCurrentContent().getEntity(currentEntity).getData();
      setLinkUrl(entityData.url || '');
      setLinkText(selectedText || entityData.text || '');
      setIsEditingLink(true);
    } else {
      // Creating new link
      setLinkUrl('');
      setLinkText(selectedText || '');
      setIsEditingLink(false);
    }
    
    setLinkDialogOpen(true);
  };

  const handleLinkSave = () => {
    if (linkUrl.trim()) {
      if (isEditingLink) {
        linkHandlers.updateLink(linkUrl, linkText);
      } else {
        linkHandlers.insertLink(linkUrl, linkText);
      }
    }
    handleLinkCancel();
  };

  const handleLinkCancel = () => {
    setLinkDialogOpen(false);
    setLinkUrl('');
    setLinkText('');
    setIsEditingLink(false);
  };

  const handleRemoveLink = () => {
    linkHandlers.removeLink();
  };

  // Embedded website handlers
  const handleEmbedClick = () => {
    setEmbedDialogOpen(true);
  };

  const handleEmbedSave = () => {
    if (embedUrl.trim()) {
      const embedData = {
        url: embedUrl,
        type: embedType,
        width: embedWidth,
        height: embedHeight
      };
      
      if (embedHandlers && embedHandlers.insertEmbed) {
        embedHandlers.insertEmbed(embedData);
      }
    }
    handleEmbedCancel();
  };

  const handleEmbedCancel = () => {
    setEmbedDialogOpen(false);
    setEmbedUrl('');
    setEmbedType('iframe');
    setEmbedWidth('100%');
    setEmbedHeight('400');
  };

  // Image URL handlers
  const handleImageUrlInsert = (imageData) => {
    if (imageHandlers && imageHandlers.handleImageUrlInsert) {
      imageHandlers.handleImageUrlInsert(imageData);
    }
  };

  const isValidUrl = (url) => {
    try {
      new URL(url);
      return true;
    } catch {
      return false;
    }
  };

  // Check if current selection has a link
  const hasLink = linkHandlers.getCurrentLinkEntity(editorState) !== null;

  return (
    <>
      <Box sx={{ display: 'flex', gap: 0 }}>
        
        <Tooltip title={hasLink ? "Edit Link" : "Insert Link"}>
          <IconButton 
            size="small" 
            onClick={handleLinkClick}
            color={hasLink ? 'primary' : 'default'}
          >
            <Link fontSize="small" />
          </IconButton>
        </Tooltip>
        
        {hasLink && (
          <Tooltip title="Remove Link">
            <IconButton size="small" onClick={handleRemoveLink}>
              <LinkOff fontSize="small" />
            </IconButton>
          </Tooltip>
        )}

        <Tooltip title="Embed Website">
          <IconButton size="small" onClick={handleEmbedClick}>
            <DatasetLinkedIcon fontSize="small" />
          </IconButton>
        </Tooltip>
        
        <Tooltip title="Insert Image">
          <span>
            <IconButton size="small" onClick={handleImageMenuClick}>
              <ImageIcon fontSize="small" />
            </IconButton>
          </span>
        </Tooltip>

      </Box>

      {/* Link Dialog */}
      <Dialog 
        open={linkDialogOpen} 
        onClose={handleLinkCancel}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>
          {isEditingLink ? 'Edit Link' : 'Insert Link'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ mt: 1 }}>
            <TextField
              autoFocus
              margin="dense"
              label="URL"
              type="url"
              fullWidth
              variant="outlined"
              value={linkUrl}
              onChange={(e) => setLinkUrl(e.target.value)}
              placeholder="https://example.com"
              sx={{ mb: 2 }}
            />
            <TextField
              margin="dense"
              label="Display Text"
              type="text"
              fullWidth
              variant="outlined"
              value={linkText}
              onChange={(e) => setLinkText(e.target.value)}
              placeholder="Link text (optional)"
              helperText="Leave empty to use the URL as display text"
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleLinkCancel}>Cancel</Button>
          <Button 
            onClick={handleLinkSave} 
            variant="contained"
            disabled={!linkUrl.trim()}
          >
            {isEditingLink ? 'Update' : 'Insert'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Image URL Dialog */}
      <ImageUrlDialog
        open={imageHandlers.imageDialogOpen}
        onClose={() => imageHandlers.setImageDialogOpen(false)}
        imageUrl={imageHandlers.imageUrl}
        setImageUrl={imageHandlers.setImageUrl}
        imageAlt={imageHandlers.imageAlt}
        setImageAlt={imageHandlers.setImageAlt}
        onInsert={handleImageUrlInsert}
      />

      {/* Embed Website Dialog */}
      <Dialog 
        open={embedDialogOpen} 
        onClose={handleEmbedCancel}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          Embed Website
        </DialogTitle>
        <DialogContent>
          <Box sx={{ mt: 1 }}>
            <TextField
              autoFocus
              margin="dense"
              label="Website URL"
              type="url"
              fullWidth
              variant="outlined"
              value={embedUrl}
              onChange={(e) => setEmbedUrl(e.target.value)}
              placeholder="https://example.com"
              sx={{ mb: 2 }}
              error={!!(embedUrl && !isValidUrl(embedUrl))}
              helperText={embedUrl && !isValidUrl(embedUrl) ? 'Please enter a valid URL' : 'Enter the URL of the website you want to embed'}
            />
            
            <FormControl fullWidth margin="dense" sx={{ mb: 2 }}>
              <InputLabel>Embed Type</InputLabel>
              <Select
                value={embedType}
                onChange={(e) => setEmbedType(e.target.value)}
                label="Embed Type"
              >
                <MenuItem value="iframe">IFrame</MenuItem>
                <MenuItem value="preview">Link Preview</MenuItem>
              </Select>
            </FormControl>

            <Box sx={{ display: 'flex', gap: 2 }}>
              <TextField
                margin="dense"
                label="Width"
                type="text"
                variant="outlined"
                value={embedWidth}
                onChange={(e) => setEmbedWidth(e.target.value)}
                placeholder="100%"
                sx={{ flex: 1 }}
                helperText="e.g., 100%, 800px"
              />
              <TextField
                margin="dense"
                label="Height"
                type="text"
                variant="outlined"
                value={embedHeight}
                onChange={(e) => setEmbedHeight(e.target.value)}
                placeholder="400"
                sx={{ flex: 1 }}
                helperText="Height in pixels"
              />
            </Box>

            {embedUrl && isValidUrl(embedUrl) && (
              <Box sx={{ mt: 2, p: 2, bgcolor: 'grey.50', borderRadius: 1 }}>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  Preview:
                </Typography>
                <Box sx={{ 
                  border: '1px solid #ddd', 
                  borderRadius: 1, 
                  overflow: 'hidden',
                  height: '200px'
                }}>
                  <iframe
                    src={embedUrl}
                    width="100%"
                    height="100%"
                    style={{ border: 'none' }}
                    title="Website Preview"
                  />
                </Box>
              </Box>
            )}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEmbedCancel}>Cancel</Button>
          <span>
            <Button 
              onClick={handleEmbedSave} 
              variant="contained"
              disabled={!embedUrl.trim() || !isValidUrl(embedUrl)}
            >
              Embed
            </Button>
          </span>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default MiscButtons;