import { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@mui/material';

const ImageUrlDialog = ({ 
  open, 
  onClose, 
  imageUrl, 
  setImageUrl, 
  imageAlt, 
  setImageAlt, 
  onInsert 
}) => {
  const [imageWidth, setImageWidth] = useState('300');
  const [imageHeight, setImageHeight] = useState('200');
  const [alignment, setAlignment] = useState('none');
  const [previewLoaded, setPreviewLoaded] = useState(false);
  const [previewError, setPreviewError] = useState(false);

  const isValidUrl = (url) => {
    try {
      new URL(url);
      return true;
    } catch {
      return false;
    }
  };

  const handleImageLoad = () => {
    setPreviewLoaded(true);
    setPreviewError(false);
  };

  const handleImageError = () => {
    setPreviewLoaded(false);
    setPreviewError(true);
  };

  const handleInsert = () => {
    if (imageUrl.trim() && isValidUrl(imageUrl)) {
      onInsert({
        url: imageUrl.trim(),
        alt: imageAlt.trim(),
        width: parseInt(imageWidth) || 300,
        height: parseInt(imageHeight) || 200,
        alignment: alignment
      });
    }
  };

  const handleCancel = () => {
    setImageUrl('');
    setImageAlt('');
    setImageWidth('300');
    setImageHeight('200');
    setAlignment('none');
    setPreviewLoaded(false);
    setPreviewError(false);
    onClose();
  };

  // Auto-detect image dimensions when URL changes
  useEffect(() => {
    if (imageUrl && isValidUrl(imageUrl)) {
      const img = new Image();
      img.onload = () => {
        const maxWidth = 500;
        const maxHeight = 400;
        let { width, height } = img;
        
        if (width > maxWidth || height > maxHeight) {
          const ratio = Math.min(maxWidth / width, maxHeight / height);
          width = Math.round(width * ratio);
          height = Math.round(height * ratio);
        }
        
        setImageWidth(width.toString());
        setImageHeight(height.toString());
        setPreviewLoaded(true);
        setPreviewError(false);
      };
      img.onerror = () => {
        setPreviewError(true);
        setPreviewLoaded(false);
      };
      img.src = imageUrl;
    } else {
      setPreviewLoaded(false);
      setPreviewError(false);
    }
  }, [imageUrl]);

  return (
    <Dialog 
      open={open} 
      onClose={handleCancel}
      maxWidth="md"
      fullWidth
    >
      <DialogTitle>
        Insert Image from URL
      </DialogTitle>
      <DialogContent>
        <Box sx={{ mt: 1 }}>
          <TextField
            autoFocus
            margin="dense"
            label="Image URL"
            type="url"
            fullWidth
            variant="outlined"
            value={imageUrl}
            onChange={(e) => setImageUrl(e.target.value)}
            placeholder="https://example.com/image.jpg"
            sx={{ mb: 2 }}
            error={!!(imageUrl && !isValidUrl(imageUrl))}
            helperText={
              imageUrl && !isValidUrl(imageUrl) 
                ? 'Please enter a valid URL' 
                : 'Enter the URL of the image you want to insert'
            }
          />
          
          <TextField
            margin="dense"
            label="Alt Text"
            type="text"
            fullWidth
            variant="outlined"
            value={imageAlt}
            onChange={(e) => setImageAlt(e.target.value)}
            placeholder="Description of the image"
            sx={{ mb: 2 }}
            helperText="Alternative text for accessibility (optional)"
          />

          <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
            <TextField
              margin="dense"
              label="Width (px)"
              type="number"
              variant="outlined"
              value={imageWidth}
              onChange={(e) => setImageWidth(e.target.value)}
              placeholder="300"
              sx={{ flex: 1 }}
              inputProps={{ min: 50, max: 1000 }}
            />
            <TextField
              margin="dense"
              label="Height (px)"
              type="number"
              variant="outlined"
              value={imageHeight}
              onChange={(e) => setImageHeight(e.target.value)}
              placeholder="200"
              sx={{ flex: 1 }}
              inputProps={{ min: 50, max: 800 }}
            />
          </Box>

          <FormControl fullWidth margin="dense" sx={{ mb: 2 }}>
            <InputLabel>Alignment</InputLabel>
            <Select
              value={alignment}
              onChange={(e) => setAlignment(e.target.value)}
              label="Alignment"
            >
              <MenuItem value="none">Default</MenuItem>
              <MenuItem value="left">Left</MenuItem>
              <MenuItem value="center">Center</MenuItem>
              <MenuItem value="right">Right</MenuItem>
            </Select>
          </FormControl>

          {/* Image Preview */}
          {imageUrl && isValidUrl(imageUrl) && (
            <Box sx={{ mt: 2, p: 2, bgcolor: 'grey.50', borderRadius: 1 }}>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Preview:
              </Typography>
              <Box sx={{ 
                display: 'flex',
                justifyContent: alignment === 'center' ? 'center' : 
                              alignment === 'right' ? 'flex-end' : 'flex-start',
                p: 1,
                border: '1px solid #ddd',
                borderRadius: 1,
                bgcolor: 'white',
                minHeight: '100px',
                alignItems: 'center'
              }}>
                {previewError ? (
                  <Box sx={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    justifyContent: 'center',
                    height: '100px',
                    color: 'error.main'
                  }}>
                    <Typography variant="body2">
                      Failed to load image
                    </Typography>
                  </Box>
                ) : (
                  <img
                    src={imageUrl}
                    alt={imageAlt || 'Preview'}
                    style={{
                      maxWidth: '100%',
                      maxHeight: '200px',
                      width: 'auto',
                      height: 'auto',
                      border: '1px solid #ddd',
                      borderRadius: '4px'
                    }}
                    onLoad={handleImageLoad}
                    onError={handleImageError}
                  />
                )}
              </Box>
              {previewLoaded && (
                <Typography variant="caption" color="text.secondary" sx={{ mt: 1 }}>
                  Image will be resized to {imageWidth} × {imageHeight} pixels
                </Typography>
              )}
            </Box>
          )}
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCancel}>Cancel</Button>
        <Button 
          onClick={handleInsert} 
          variant="contained"
          disabled={!imageUrl.trim() || !isValidUrl(imageUrl)}
        >
          Insert Image
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ImageUrlDialog;