import { RichUtils, EditorState, Modifier, AtomicBlockUtils } from 'draft-js';

export const useEditorHandlers = (editorState, onEditorStateChange) => {
  const handleKeyCommand = (command, editorState) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      onEditorStateChange(newState);
      return 'handled';
    }
    return 'not-handled';
  };

  const handleInlineStyle = (style) => {
    onEditorStateChange(RichUtils.toggleInlineStyle(editorState, style));
  };

  const handleBlockType = (blockType) => {
    onEditorStateChange(RichUtils.toggleBlockType(editorState, blockType));
  };

  const handleAlignment = (alignment) => {
    const selection = editorState.getSelection();
    const currentContent = editorState.getCurrentContent();
    const blockKey = selection.getStartKey();
    const block = currentContent.getBlockForKey(blockKey);
    
    const blockData = block.getData();
    const currentAlign = blockData.get('text-align');
    const newAlignment = currentAlign === alignment ? undefined : alignment;
    
    const newBlockData = newAlignment 
      ? blockData.set('text-align', newAlignment)
      : blockData.delete('text-align');
    
    const newContentState = Modifier.setBlockData(
      currentContent,
      selection,
      newBlockData
    );
    
    const newEditorState = EditorState.push(
      editorState,
      newContentState,
      'change-block-data'
    );
    
    onEditorStateChange(newEditorState);
  };

  const handleUndo = () => {
    onEditorStateChange(EditorState.undo(editorState));
  };

  const handleRedo = () => {
    onEditorStateChange(EditorState.redo(editorState));
  };

  return {
    handleKeyCommand,
    handleInlineStyle,
    handleBlockType,
    handleAlignment,
    handleUndo,
    handleRedo
  };
};