import { FormControl, Select, MenuItem, Box } from '@mui/material';

const FontControls = ({ fontHandlers }) => (
  <Box display="flex" alignItems="center" gap={0.5}>
    <FormControl size="small" sx={{ width: 100 }}>
      <Select
        value={fontHandlers.getCurrentFontFamily()}
        onChange={(e) => fontHandlers.handleFontFamily(e.target.value)}
        displayEmpty
        sx={{ height: 32 }}
      >
        {fontHandlers.fontFamilies.map(family => (
          <MenuItem key={family} value={family} sx={{ fontFamily: family }}>
            {family}
          </MenuItem>
        ))}
      </Select>
    </FormControl>

    <FormControl size="small" sx={{ minWidth: 70 }}>
      <Select
        value={fontHandlers.getCurrentFontSize()}
        onChange={(e) => fontHandlers.handleFontSize(e.target.value)}
        sx={{ height: 32 }}
      >
        {fontHandlers.fontSizes.map(size => (
          <MenuItem key={size} value={size}>
            {size}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  </Box>
);

export default FontControls;