export const createStyleMap = (fontSizes, fontFamilies) => {
  const styleMap = {};

  // 字体大小样式
  if (fontSizes) {
    fontSizes.forEach(size => {
      styleMap[`FONTSIZE_${size}`] = {
        fontSize: `${size}px`,
      };
    });
  }

  // 字体系列样式
  if (fontFamilies) {
    fontFamilies.forEach(family => {
      const key = family.replace(/\s+/g, '_').toUpperCase();
      styleMap[`FONTFAMILY_${key}`] = {
        fontFamily: family,
      };
    });
  }

  // 文字颜色样式
  const colors = [
    { name: 'Black', value: '#000000' },
    { name: 'Red', value: '#FF0000' },
    { name: 'Green', value: '#00FF00' },
    { name: 'Blue', value: '#0000FF' },
    { name: 'Orange', value: '#FFA500' },
    { name: 'Purple', value: '#800080' },
    { name: 'Pink', value: '#FFC0CB' },
    { name: 'Brown', value: '#A52A2A' },
    { name: 'Gray', value: '#808080' },
    { name: 'Navy', value: '#000080' },
    { name: 'Teal', value: '#008080' },
    { name: 'Maroon', value: '#800000' }
  ];

  colors.forEach(color => {
    const colorKey = color.value.replace('#', '');
    styleMap[`COLOR_${colorKey}`] = {
      color: color.value,
    };
  });

  // 高亮颜色样式
  const highlightColors = [
    { name: 'Yellow', value: '#FFFF00' },
    { name: 'Green', value: '#90EE90' },
    { name: 'Pink', value: '#FFB6C1' },
    { name: 'Blue', value: '#ADD8E6' },
    { name: 'Orange', value: '#FFE4B5' },
    { name: 'Purple', value: '#DDA0DD' },
    { name: 'Red', value: '#FFA07A' },
    { name: 'Gray', value: '#D3D3D3' }
  ];

  highlightColors.forEach(color => {
    const colorKey = color.value.replace('#', '');
    styleMap[`HIGHLIGHT_${colorKey}`] = {
      backgroundColor: color.value,
    };
  });

  // 移除高亮的特殊样式
  styleMap['HIGHLIGHT_NONE'] = {
    backgroundColor: 'transparent',
  };

  return styleMap;
};

export const getBlockStyle = (block) => {
  const blockData = block.getData();
  const textAlign = blockData.get('text-align');
  return textAlign ? `text-align-${textAlign}` : null;
};

export const getCurrentBlockData = (editorState) => {
  const selection = editorState.getSelection();
  const currentContent = editorState.getCurrentContent();
  const blockKey = selection.getStartKey();
  const block = currentContent.getBlockForKey(blockKey);
  return {
    blockType: block.getType(),
    blockData: block.getData(),
    currentAlignment: block.getData().get('text-align')
  };
};